/* Задаємо базову ширину адаптивного контейнера */
.container {
  min-width: 320px;
  margin: 0 auto;
}
@media screen and (min-width: 320px) {
    .container {
        max-width: 767px;
    }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 1279px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}
